/**
 * 
 * 
 */

export class EventManager {
  private listeners: Array<() => void> = [];
  private timers: Set<number> = new Set();
  private intervals: Set<number> = new Set();
  private isDestroyed: boolean = false;

  /**
   * 
   * @param event 
   * @param handler 
   * @returns 
   */
  addGlobalListener(event: string, handler: Function): () => void {
    if (this.isDestroyed) {
      console.warn('[EventManager] ');
      return () => {};
    }

    const cleanup = (window as any).eventOn(event, handler);
    if (cleanup && typeof cleanup === 'function') {
      this.listeners.push(cleanup);
      return cleanup;
    } else {
      console.warn(`[EventManager]  ${event} `);
      return () => {};
    }
  }

  /**
   * DOM
   * @param element DOM
   * @param event 
   * @param handler 
   * @param options 
   */
  addDOMListener(
    element: Element | Window | Document,
    event: string,
    handler: EventListener,
    options?: boolean | AddEventListenerOptions,
  ): void {
    if (this.isDestroyed) {
      console.warn('[EventManager] DOM');
      return;
    }

    element.addEventListener(event, handler, options);

    // 
    const cleanup = () => {
      element.removeEventListener(event, handler, options);
    };

    this.listeners.push(cleanup);
  }

  /**
   * setTimeout
   * @param callback 
   * @param delay 
   * @returns ID
   */
  setTimeout(callback: Function, delay: number): number {
    if (this.isDestroyed) {
      console.warn('[EventManager] ');
      return -1;
    }

    const timerId = window.setTimeout(() => {
      this.timers.delete(timerId);
      try {
        callback();
      } catch (error) {
        console.error('[EventManager] :', error);
      }
    }, delay);

    this.timers.add(timerId);
    return timerId;
  }

  /**
   * setInterval
   * @param callback 
   * @param interval 
   * @returns ID
   */
  setInterval(callback: Function, interval: number): number {
    if (this.isDestroyed) {
      console.warn('[EventManager] ');
      return -1;
    }

    const intervalId = window.setInterval(() => {
      try {
        callback();
      } catch (error) {
        console.error('[EventManager] :', error);
      }
    }, interval);

    this.intervals.add(intervalId);
    return intervalId;
  }

  /**
   * 
   * @param timerId ID
   */
  clearTimeout(timerId: number): void {
    if (this.timers.has(timerId)) {
      window.clearTimeout(timerId);
      this.timers.delete(timerId);
    }
  }

  /**
   * 
   * @param intervalId ID
   */
  clearInterval(intervalId: number): void {
    if (this.intervals.has(intervalId)) {
      window.clearInterval(intervalId);
      this.intervals.delete(intervalId);
    }
  }

  /**
   * 
   */
  cleanup(): void {
    if (this.isDestroyed) {
      return;
    }

    // 
    this.listeners.forEach(cleanup => {
      try {
        cleanup();
      } catch (error) {
        console.error('[EventManager] :', error);
      }
    });
    this.listeners = [];

    // 
    this.timers.forEach(timerId => {
      window.clearTimeout(timerId);
    });
    this.timers.clear();

    // 
    this.intervals.forEach(intervalId => {
      window.clearInterval(intervalId);
    });
    this.intervals.clear();

    this.isDestroyed = true;
    //console.log('[EventManager] ');
  }

  /**
   * 
   */
  getStatus(): {
    listenersCount: number;
    timersCount: number;
    intervalsCount: number;
    isDestroyed: boolean;
  } {
    return {
      listenersCount: this.listeners.length,
      timersCount: this.timers.size,
      intervalsCount: this.intervals.size,
      isDestroyed: this.isDestroyed,
    };
  }

  /**
   * 
   */
  isDestroyed_(): boolean {
    return this.isDestroyed;
  }
}

/**
 * 
 * 
 */
export const globalEventManager = new EventManager();

/**
 * 
 */
if (typeof window !== 'undefined') {
  window.addEventListener('beforeunload', () => {
    globalEventManager.cleanup();
  });
}
