// 
export interface Theme {
  name: string;
  displayName: string;
  colors: {
    // 
    phoneFrame: string;
    phoneFrameGradient: string[];

    // 
    primary: string;
    light: string;
    ultraLight: string;
    soft: string;
    deep: string;

    // 
    gradientStart: string;
    gradientEnd: string;

    // 
    textColor: string;
    textPrimary: string;
    textSecondary: string;

    // 
    borderColor: string;
    bubbleShadow: string;

    // 
    bubbleThemBg: string;
    bubbleMeBg: string;

    // 
    bubbleThemText: string;
    bubbleMeText: string;

    // 
    headerBg: string;

    // 
    statusBarColor: string;

    // 
    dynamicIslandColor: string;

    // 
    appBgColor: string;

    // 
    settingsBgColor: string;

    // 
    settingsCardBgColor: string;

    // 
    shadowColor: string;
    shadowColorRgba: string;

    // 
    isDarkMode: boolean;

    // 
    defaultWallpapers: {
      chat: string; // URL
      home: string;
      settings: string;
    };

    // 
    useSingleColorIcons?: boolean;
  };
}

