/**
 * 
 * 
 */

import { logger } from './debug-logger';

/**
 * JSON
 * @param jsonString JSON
 * @param defaultValue 
 * @returns 
 */
export function safeJsonParse<T = any>(jsonString: string, defaultValue: T | null = null): T | null {
  try {
    return JSON.parse(jsonString);
  } catch (error) {
    logger.warn('Utils', 'JSON', { jsonString: jsonString.substring(0, 100), error });
    return defaultValue;
  }
}

/**
 * JSON
 * @param obj 
 * @param defaultValue 
 * @returns JSON
 */
export function safeJsonStringify(obj: any, defaultValue: string = '{}'): string {
  try {
    return JSON.stringify(obj);
  } catch (error) {
    logger.warn('Utils', 'JSON', { error });
    return defaultValue;
  }
}

/**
 *  - lodashdebounce
 * @param func 
 * @param wait 
 * @param options lodash debounce
 * @returns 
 */
export function debounce<T extends (...args: any[]) => any>(
  func: T,
  wait: number,
  options?: { leading?: boolean; trailing?: boolean; maxWait?: number },
): T {
  return (window as any)._.debounce(func, wait, options);
}

/**
 *  - lodashthrottle
 * @param func 
 * @param wait 
 * @param options lodash throttle
 * @returns 
 */
export function throttle<T extends (...args: any[]) => any>(
  func: T,
  wait: number,
  options?: { leading?: boolean; trailing?: boolean },
): T {
  return (window as any)._.throttle(func, wait, options);
}

/**
 * 
 * @param ms 
 * @returns Promise
 */
export function delay(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms));
}

/**
 * DOM - jQuery
 * @param selector CSS
 * @param context document
 * @returns jQuery
 */
export function $safe(selector: string, context?: Element | Document): JQuery<HTMLElement> {
  try {
    return context ? $(selector, context) : $(selector);
  } catch (error) {
    logger.warn('Utils', 'jQuery', { selector, error });
    return $(); // jQuery
  }
}

/**
 * DOM
 * @param selector CSS
 * @param context document
 * @returns null
 */
export function safeQuerySelector<T extends Element = Element>(
  selector: string,
  context: Document | Element = document,
): T | null {
  try {
    return context.querySelector<T>(selector);
  } catch (error) {
    logger.warn('Utils', 'DOM', { selector, error });
    return null;
  }
}

/**
 * DOM- jQuery
 * @param selector CSS
 * @param context document
 * @returns 
 */
export function safeQuerySelectorAll<T extends Element = Element>(
  selector: string,
  context: Document | Element = document,
): T[] {
  try {
    const $elements = context ? $(selector, context) : $(selector);
    return $elements.toArray() as unknown as T[];
  } catch (error) {
    logger.warn('Utils', 'DOM', { selector, error });
    return [];
  }
}

/**
 * 
 * @param bytes 
 * @returns 
 */
export function formatFileSize(bytes: number): string {
  if (bytes === 0) return '0 B';

  const k = 1024;
  const sizes = ['B', 'KB', 'MB', 'GB', 'TB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
}

/**
 * 
 * @param timestamp 
 * @param format 
 * @returns 
 */
export function formatTimestamp(timestamp: number, format: 'full' | 'time' | 'date' = 'full'): string {
  const date = new Date(timestamp);

  switch (format) {
    case 'time':
      return date.toLocaleTimeString('zh-CN', { hour12: false });
    case 'date':
      return date.toLocaleDateString('zh-CN');
    case 'full':
    default:
      return date.toLocaleString('zh-CN', { hour12: false });
  }
}

/**
 * ID
 * @param prefix 
 * @returns ID
 */
export function generateId(prefix: string = 'id'): string {
  return `${prefix}-${Date.now()}-${Math.random().toString(36).substring(2, 11)}`;
}

/**
 *  - lodashcloneDeep
 * @param obj 
 * @returns 
 */
export function deepClone<T>(obj: T): T {
  return (window as any)._.cloneDeep(obj);
}

/**
 * 
 * @returns 
 */
export function isMobileDevice(): boolean {
  return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
}

/**
 * 
 * @returns 
 */
export function isTouchDevice(): boolean {
  return 'ontouchstart' in window || navigator.maxTouchPoints > 0;
}

/**
 * 
 */
export const SafeStorage = {
  /**
   * 
   * @param key 
   * @param defaultValue 
   * @returns 
   */
  getItem<T = string>(key: string, defaultValue: T | null = null): T | null {
    try {
      const item = localStorage.getItem(key);
      if (item === null) return defaultValue;

      // JSON
      try {
        return JSON.parse(item);
      } catch {
        return item as unknown as T;
      }
    } catch (error) {
      logger.warn('Utils', '', { key, error });
      return defaultValue;
    }
  },

  /**
   * 
   * @param key 
   * @param value 
   * @returns 
   */
  setItem(key: string, value: any): boolean {
    try {
      const serialized = typeof value === 'string' ? value : JSON.stringify(value);
      localStorage.setItem(key, serialized);
      return true;
    } catch (error) {
      logger.warn('Utils', '', { key, error });
      return false;
    }
  },

  /**
   * 
   * @param key 
   * @returns 
   */
  removeItem(key: string): boolean {
    try {
      localStorage.removeItem(key);
      return true;
    } catch (error) {
      logger.warn('Utils', '', { key, error });
      return false;
    }
  },

  /**
   * 
   * @returns 
   */
  clear(): boolean {
    try {
      localStorage.clear();
      return true;
    } catch (error) {
      logger.warn('Utils', '', { error });
      return false;
    }
  },
};

/**
 * 
 */
export const ErrorHandler = {
  /**
   * 
   * @param fn 
   * @param errorMessage 
   * @returns null
   */
  async safeAsync<T>(fn: () => Promise<T>, errorMessage?: string): Promise<T | null> {
    try {
      return await fn();
    } catch (error) {
      logger.error('ErrorHandler', errorMessage || '', { error });
      return null;
    }
  },

  /**
   * 
   * @param fn 
   * @param errorMessage 
   * @returns null
   */
  safeSync<T>(fn: () => T, errorMessage?: string): T | null {
    try {
      return fn();
    } catch (error) {
      logger.error('ErrorHandler', errorMessage || '', { error });
      return null;
    }
  },
};
