export function addTestEntry(blmxManager: any, entryType: string): void {
  let entry: any = null;

  switch (entryType) {
    case 'text-user':
      entry = {
        id: generateId('msg'),
        type: 'message',
        sender: 'me',
        content: '',
      };
      break;
    case 'text-char':
      entry = {
        id: generateId('msg'),
        type: 'message',
        sender: 'them',
        content: 'AI',
      };
      break;
    case 'sticker-user':
      entry = {
        id: generateId('sticker'),
        type: 'sticker',
        sender: 'me',
        content: '',
      };
      break;
    case 'sticker-char':
      entry = {
        id: generateId('sticker'),
        type: 'sticker',
        sender: 'them',
        content: '',
      };
      break;
    case 'image-user':
      entry = {
        id: generateId('img'),
        type: 'image',
        sender: 'me',
        content: {
          type: 'url',
          value: 'https://gitgud.io/lolodesu/lolobabytutorial/-/raw/master/lologame//wallpaper/_1.jpg',
        },
      };
      break;
    case 'image-char':
      entry = {
        id: generateId('img'),
        type: 'image',
        sender: 'them',
        content: {
          type: 'url',
          value: 'https://gitgud.io/lolodesu/lolobabytutorial/-/raw/master/lologame//wallpaper/_1.jpg',
        },
      };
      break;
    case 'voice-user':
      entry = {
        id: generateId('voice'),
        type: 'voice',
        sender: 'me',
        content: {
          text: '',
          duration: 3,
        },
      };
      break;
    case 'voice-char':
      entry = {
        id: generateId('voice'),
        type: 'voice',
        sender: 'them',
        content: {
          text: 'AI',
          duration: 4,
        },
      };
      break;
    case 'transfer-user':
      entry = {
        id: generateId('transfer'),
        type: 'transfer',
        sender: 'me',
        content: JSON.stringify({
          amount: '88.88',
          note: '',
          status: 'sent',
        }),
        data: {
          amount: '88.88',
          note: '',
          status: 'sent',
        },
      };
      break;
    case 'transfer-char':
      entry = {
        id: generateId('transfer'),
        type: 'transfer',
        sender: 'them',
        content: JSON.stringify({
          amount: '66.66',
          note: 'AI',
          status: 'sent',
        }),
        data: {
          amount: '66.66',
          note: 'AI',
          status: 'sent',
        },
      };
      break;
    case 'gift-user':
      entry = {
        id: generateId('gift'),
        type: 'gift',
        sender: 'me',
        content: JSON.stringify({
          name: '',
          price: '99.99',
          status: 'sent',
        }),
        data: {
          name: '',
          price: '99.99',
          status: 'sent',
        },
      };
      break;
    case 'gift-char':
      entry = {
        id: generateId('gift'),
        type: 'gift',
        sender: 'them',
        content: JSON.stringify({
          name: '',
          price: '39.99',
          status: 'sent',
        }),
        data: {
          name: '',
          price: '39.99',
          status: 'sent',
        },
      };
      break;
    case 'moment-user':
      entry = {
        key: 'USER_MOMENT',
        data: {
          text: '',
          image: 'https://gitgud.io/lolodesu/lolobabytutorial/-/raw/master/lologame//wallpaper/_2.jpg',
          image_type: 'url',
          date: getCurrentDate(),
          time: getCurrentTime(),
        },
      };
      break;
    case 'moment-char':
      entry = {
        key: 'CHAR_MOMENT',
        data: {
          text: 'AI',
          image: 'https://gitgud.io/lolodesu/lolobabytutorial/-/raw/master/lologame//wallpaper/_1.jpg',
          image_type: 'url',
          date: getCurrentDate(),
          time: getCurrentTime(),
        },
      };
      break;
    case 'event-short':
      entry = {
        type: 'event',
        content: {
          date: getCurrentDate(),
          time: getCurrentTime(),
          description: '',
        },
      };
      break;
    case 'event-long':
      entry = {
        type: 'event',
        content: {
          date: getCurrentDate(),
          time: getCurrentTime(),
          description:
            '',
        },
      };
      break;
    case 'time':
      entry = {
        type: 'time',
        content: {
          date: getCurrentDate(),
          time: getCurrentTime(),
        },
      };
      break;
    default:
      console.error('[DEBUG] :', entryType);
      return;
  }

  if (entry) {
    blmxManager.addEntry(entry);
    blmxManager.persistLogToStorage();
    console.log(`[DEBUG] : ${entryType}`, entry);
  }
}

