// 
export function showDebugInfo(appController: any): void {
  console.log('[BLMX Debug] :');

  try {
    // 
    const rawAiResponse = appController.getRawAiResponse ? appController.getRawAiResponse() : '';
    const fullPrompt = appController.getFullPrompt ? appController.getFullPrompt() : '';
    const latestAiRawResponse = appController.getLatestAiRawResponse
      ? appController.getLatestAiRawResponse()
      : '';
    const latestSentPrompt = appController.getLatestSentPrompt ? appController.getLatestSentPrompt() : '';

    console.log('- AI:', rawAiResponse);
    console.log('- :', fullPrompt);
    console.log('- AI:', latestAiRawResponse);
    console.log('- AI:', latestSentPrompt);
  } catch (error) {
    console.error('[BLMX Debug] :', error);
  }
}
